<template>
  <div id="map-container" class="map" ref="map">

  </div>
</template>

<script>
    import msgs from "../utils/messages";

    import * as mapStyles from '../../src/utils/map-styles'
    import {fromLonLat} from 'ol/proj'
    import Feature from 'ol/Feature';
    import * as Control from 'ol/control'
    // eslint-disable-next-line no-unused-vars
    import {circular} from 'ol/geom/Polygon';
    import 'ol/ol.css'
    // eslint-disable-next-line no-unused-vars
    import {easeOut} from 'ol/easing';
    // eslint-disable-next-line no-unused-vars
    import {getVectorContext} from 'ol/render';
    // eslint-disable-next-line no-unused-vars
    import {unByKey} from 'ol/Observable';

    import VectorLayer from 'ol/layer/Vector'
    import VectorSource from 'ol/source/Vector'
    import ClusterSource from 'ol/source/Cluster'

    import ScaleLine from 'ol/control/ScaleLine'
    import FullScreen from 'ol/control/FullScreen'

    import ZoomSlider from 'ol/control/ZoomSlider'

    import Map from 'ol/Map'
    import View from 'ol/View'
    import TileLayer from 'ol/layer/Tile'

    import Projection from 'ol/proj/Projection'
    import Point from 'ol/geom/Point'

    import OSM from 'ol/source/OSM'
    // eslint-disable-next-line no-unused-vars
    import {iconSquareStyle} from "../../src/utils/map-styles";
    // import Locate from "../components/Locate"

    let map

    let vectorSourcePoi
    let vectorSourceCommon
    let vectorSourceClusterPoi
    let vectorLayerPoi
    let vectorLayerCommon

    // eslint-disable-next-line no-unused-vars

    let OSMLayer

    let currentResolution = null
    let isPoiClustering = true
    const maxZoomForClustering = 13
    let zoom = 10
    let animationDuration = 3000;

    // eslint-disable-next-line no-unused-vars
    const fromProjection = new Projection({code: 'EPSG:4326'}) // transform from WGS 1984
    const toProjection = new Projection({code: 'EPSG:3857'}) // transform from WGS 1984

    let locate
    let OSMSource
    // eslint-disable-next-line no-unused-vars

    export default {
        name: 'Map',
        // components: {
        //     Locate
        // },
        data: function () {
            // console.log('data')
            return {
                navigatorPermissionsGeo: null
            };
        },
        created() {
            this.getNavigationPermission()
        },
        mounted() {
            if (msgs[this.$route.query.message]) {
                this.$message(msgs[this.$route.query.message])
            }

            this.initMap()


        },
        watch: {
            navigatorPermissionsGeo() {
                this.checkNavigatorPermissions()
            }
        },
        methods: {
            async getNavigationPermission() {
                this.navigatorPermissionsGeo = await navigator.permissions.query({name: 'geolocation'})
            },
            initMap() {
                OSMSource = new OSM({
                    crossOrigin: null
                })
                //
                // var count = 20000;
                // var features = new Array(count);
                // var e = 4500000;
                // for (var i = 0; i < count; ++i) {
                //     var coordinates = [2 * e * Math.random() - e, 2 * e * Math.random() - e];
                //     features[i] = new Feature(new Point(coordinates));
                // }

                OSMLayer = new TileLayer({
                    type: 'base',
                    title: 'OpenStreetMap',
                    source: OSMSource,
                    // renderMode: 'image',
                    visible: true
                })

                vectorSourcePoi = new VectorSource({wrapX: false})
                vectorSourceCommon = new VectorSource({
                    // features: features,
                    wrapX: false
                })

                vectorSourceClusterPoi = new ClusterSource({
                    distance: 20,
                    source: vectorSourcePoi,
                    geometryFunction: this.geometryForCluster
                })
                vectorLayerPoi = new VectorLayer({
                    title: 'vectorLayerPoi',
                    source: vectorSourceClusterPoi,
                    // renderMode: 'image',
                    style: this.poiStyleBase
                })
                vectorLayerCommon = new VectorLayer({
                    title: 'vectorLayerCommon',
                    source: vectorSourceCommon,
                    // renderMode: 'image',
                    // style: mapStyles.iconSquareStyle
                })
                map = new Map({
                    controls: Control.defaults({
                        attribution: false,
                    }),
                    target: 'map-container',
                    layers: [OSMLayer, vectorLayerPoi, vectorLayerCommon],
                    view: new View({
                        projection: toProjection.getCode(),
                        // center: fromLonLat([67.1233, 41]),
                        center: fromLonLat([0, 0]),
                        // minZoom: 5,
                        maxZoom: 18,
                        zoom: 1
                    })
                })

                this.$nextTick(function () {

                    map.getControls().extend([
                        new ScaleLine({
                            units: 'metric',
                            minWidth: 100
                        }),
                        new FullScreen(),
                        new ZoomSlider(),
                    ])

                });

            },
            geometryForCluster(feature) {
                let geom = feature.getGeometry()

                if (geom.getType() === 'Point') {
                    return geom
                } else if (geom.getType() === 'Polygon') {
                    return geom.getInteriorPoint()
                } else if (geom.getType() === 'LineString') {
                    let linePoint = new Point(geom.getLastCoordinate())
                    return linePoint
                } else if (geom.getType() === 'Circle') {
                    let circleGeom = new Point(geom.getLastCoordinate())
                    return circleGeom
                }

//    console.log('geometryForCluster is null')
                return null
            },
            poiStyleBase(feature, resolution) {
                let size = 0

                if (resolution !== currentResolution) {
                    currentResolution = resolution
                }

                if (zoom <= maxZoomForClustering && isPoiClustering === true) {
                    size = feature.get('features').length
                }

                return mapStyles.poiStyle(feature, resolution, size, isPoiClustering)
            },
            checkNavigatorPermissions() {
                console.log('this.navigatorPermissionsGeo.state:' + this.navigatorPermissionsGeo.state)

                if (this.navigatorPermissionsGeo.state === 'granted') {
                    this.addFileLocationControl()
                    this.getUserLocation()
                } else if (this.navigatorPermissionsGeo.state === 'prompt') {
                    this.addFileLocationControl()
                } else if (this.navigatorPermissionsGeo.state === 'denied') {
                    console.log('denied')
                }
                this.navigatorPermissionsGeo.onchange = function () {
                    console.log('onchange geolocation')
                }
            },
            addFileLocationControl() {
                map.addControl(new Control.Control({
                    element: this.createFindLocationControl()
                }));
                this.addFindLocationListener()
            },
            createFindLocationControl() {
                // console.log('createFindLocationControl')
                locate = document.createElement('div');
                locate.className = 'ol-control ol-unselectable locate';
                locate.innerHTML = '<button title="Locate me"></button>';
                return locate
            },
            getUserLocation() {
                if (navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition(showUserLocation);
                }
            },
            addFindLocationListener() {
                locate.addEventListener('click', function () {
                    if (navigator.geolocation) {
                        navigator.geolocation.getCurrentPosition(showUserLocation);
                    }
                });
            }
        }
    }

    window.setInterval(getRandomFeatureForAnimate, 2000);

    // eslint-disable-next-line no-unused-vars
    function getRandomFeatureForAnimate() {
        flash(vectorSourceCommon.getFeatureById(-999))
    }

    function flash(feature) {
        let start = new Date().getTime();
        let listenerKey = OSMLayer.on('postrender', animate);

        function animate(event) {
            let vectorContext = getVectorContext(event);
            let frameState = event.frameState;
            let flashGeom = feature.getGeometry().clone();
            let elapsed = frameState.time - start;
            let elapsedRatio = elapsed / animationDuration;
            // radius will be 5 at start and 30 at end.
            let radius = easeOut(elapsedRatio) * 25 + 5;
            let opacity = easeOut(1 - elapsedRatio);

            vectorContext.setStyle(mapStyles.featureAnimationStyle(radius, opacity));
            vectorContext.drawGeometry(flashGeom);
            if (elapsed > animationDuration) {
                unByKey(listenerKey);
                return;
            }
            // tell OpenLayers to continue postrender animation
            map.render();
        }
    }

    function showUserLocation(position) {
        // console.log('showUserLocation')
        if (!vectorSourceCommon.getFeatureById(-999)) {
            createUserLocationPoint([position.coords.longitude, position.coords.latitude], position)
        }

        map.getView().setCenter(fromLonLat([position.coords.longitude, position.coords.latitude]));
        map.getView().setZoom(16);
    }


    function createUserLocationPoint(coords, pos) {
        // console.log('createUserLocationPoint')

        let positionFeature = new Feature();
        positionFeature.setId(-999)
        positionFeature.set('featureStyle', 'locationPoint')
        positionFeature.setStyle(mapStyles.iconSquareStyle(positionFeature));
        positionFeature.setGeometry(new Point(fromLonLat(coords)))

        vectorSourceCommon.addFeature(positionFeature);

        const accuracy = circular(coords, pos.coords.accuracy);

        vectorSourceCommon.addFeature(new Feature(accuracy.transform('EPSG:4326', map.getView().getProjection())));

    }

    //region Help Functions

    //endregion
</script>

<style>
  #map-container {
    width: 100%;
    height: 93%;
    /*position: fixed;*/
    position: fixed;
    margin: 0;
    padding: 0;
  }

  /*.ol-scale-line {*/
  /*  left: unset !important;*/
  /*  right: 15px !important;*/
  /*  z-index: 9999 !important;*/
  /*  bottom: 95px !important;*/
  /*}*/

  .locate {
    top: 20em;
    left: .5em;
  }

  .ol-full-screen {
    left: 4em;
    top: .6em;
    display: inline-table;
  }
</style>