function getIconPathByIconId(id) {
    // console.log('getIconPathByIconId')
    // console.log(iconId)
    return this.$iconsImgUrl + 'pin-' + id + '.svg'
}

export function defaultFeatureStyle(feature, zoomedForCluster) {
    // console.log('defaultFeatureStyle!!!')
    let pinId = feature.get('pinId')
    let fontSize = 11
    let titleColor = feature.get('titleColor')
    let featureType = feature.getGeometry().getType()

    console.log(featureType)
    console.log(this.$featureTypePoint)

    if (featureType === 'Point') {
        let svgImg = getIconPathByIconId(pinId)

        if (zoomedForCluster) {
            return [
                new Style({
                    image: new Icon({
                        opacity: 1,
                        scale: 1,
                        src: svgImg
//            imgSize:[32,32]
                    }),
                    text: new Text({
                        font: '11px Arial',
                        scale: 1,
                        text: mapUtils.stringDivider(feature.get('title'), 16, '\n'),
                        offsetY: 29,
//          fill: new Fill({color: 'rgb(0,0,0)'}),
                        stroke: new Stroke({
                            color: 'rgba(20, 40, 67, 0.3)',
                            width: 1
                        }),
                        overflow: true,
                        placement: 'Point',
                        geometry: function (feature) {
                            let originalFeature = feature.get('features')

                            if (originalFeature !== undefined && originalFeature !== null) {
                                return originalFeature[0].getGeometry()
                            } else {
//                  console.log(originalFeature)
                                return feature.getGeometry()
                            }
                        }
                    })
                })
            ]
        } else {
            return [
                new Style({
                    image: new Icon({
                        opacity: 1,
                        scale: 1,
                        src: svgImg
//            imgSize:[32,32]
                    }),
                    text: new Text({
                        font: '11px Arial',
                        scale: 1,
                        text: mapUtils.stringDivider(feature.get('title'), 16, '\n'),
                        offsetY: 29,
//          fill: new Fill({color: 'rgb(0,0,0)'}),
                        stroke: new Stroke({
                            color: 'rgba(20, 40, 67, 0.3)',
                            width: 1
                        }),
                        overflow: true,
                        placement: 'Point'
//            text: mapUtils.stringDivider(feature.get('title'), 16, '\n'),

                    })
                })
            ]
        }
    } else {
        if (zoomedForCluster) {
            return [
                new Style({
                    fill: new Fill({
                        color: mapUtils.hexToRgba(feature.get('polygonColor'))
                    }),
                    stroke: new Stroke({
                        color: '#1589FF',
                        width: 1
                    }),
                    text: new Text({
                        font: fontSize + 'px Arial',
                        fill: new Fill({color: titleColor}),
                        scale: 1,
                        stroke: new Stroke({
                            color: titleColor, width: 0.5
                        }),
                        text: mapUtils.stringDivider(feature.get('title'), 16, '\n'),
                        overflow: false,
                        placement: 'Point'
                    }),
                    geometry: function (feature) {
                        let originalFeature = feature.get('features')

                        if (originalFeature !== undefined && originalFeature !== null) {
                            return originalFeature[0].getGeometry()
                        } else {
//                console.log(originalFeature)
                            return feature.getGeometry()
                        }
                    }
                })
            ]
        } else {
            let lineWidth = 1

            if (featureType === this.$featureTypeLineString) {
//          console.log(feature.get('lineWidth'))
//          console.log(feature)
                if (feature.get('lineWidth') !== null && feature.get('lineWidth') !== undefined) {
                    lineWidth = feature.get('lineWidth')
                }
            }
            return [
                new Style({
                    fill: new Fill({
                        color: mapUtils.hexToRgba(feature.get('polygonColor'))
                    }),
                    stroke: new Stroke({
                        color: '#1589FF',
                        width: lineWidth
                    }),
                    text: new Text({
                        font: fontSize + 'px Arial',
                        fill: new Fill({color: titleColor}),
                        scale: 1,
                        stroke: new Stroke({
                            color: titleColor, width: 0.5
                        }),
                        text: mapUtils.stringDivider(feature.get('title'), 16, '\n'),
                        overflow: false,
                        placement: 'Point'
                    })
                })
            ]
        }
    }
}

export function poiStyle(feature, resolution, size, isPoiClustering) {
    let style

    if (size > 1) {
        style = poiClusterStyle(size)
    } else {
        if (isPoiClustering) {
            let originalFeature = feature.get('features')[0]
            style = defaultFeatureStyle(originalFeature, true)
        } else {
            style = defaultFeatureStyle(feature, true)
        }
    }
    return style

}

function poiClusterStyle(size) {
    return [
        new Style({
            zIndex: 12,
            image: new RegularShape({
                fill: new Fill({
                    color: 'rgba(255, 165, 60, 0.9)'
                }),
                stroke: new Stroke({
                    color: '#fff'
                }),
                points: 3,
                radius: 20,
                angle: 45
            }),
            text: new Text({
                text: size.toString(),
                fill: new Fill({
                    color: '#fff'
                })
            })
        })
    ]
}

export function userLocationStyle() {
    return [
        new Style({
            image: new CircleStyle({
                radius: 6,
                fill: new Fill({
                    color: '#3399CC'
                }),
                stroke: new Stroke({
                    color: '#fff',
                    width: 2
                })
            })
        })
    ]
}

export function featureAnimationStyle(radius, opacity) {
    return new Style({
        image: new CircleStyle({
            radius: radius,
            stroke: new Stroke({
                color: 'rgba(255, 0, 0, ' + opacity + ')',
                width: 0.25 + 1
            })
        })
    })
}

